
.tdrow{
    display: flex;
    flex-direction: row;
    align-items: center;
}
.tdCol{
    display: flex;
    flex-direction: column;
}
td{
    text-align: left;
    box-shadow: inset 0px -2px 0px #e4e4e42c;
}
#selectedProject {
    padding: 7px;
    border-radius: 10px;
    background-color: #e4e4e42c;
    color: white;
    outline: none;
    border: none;
}
#selectedProject option{
    color: black;
}