.Side_nav {
    width: 15vw;
    height: 100vh;
    background-color: rgb(36, 39, 49);
    position: fixed;
    border-right: 1px;
    display: flex;
    flex-direction: column;
    box-shadow: 1px 0px 1px rgb(139, 139, 139);;
    z-index: 1;
}
.logo{
    align-self: center;
    margin-top: 10px;
}
.logo img{
    width: 8vw;
    align-self: center;
}
.adminTools{
    font-size: smaller;
    text-align: start;
    color: #808191;
    margin: 20px;
}
.adminTools ul{
    list-style: none;
}
.adminTools ul li{
    list-style: none;
    font-weight: 500;
    padding: 1vw;
    width: 10vw;
    margin-left: -2vw;
    border-radius: 5px;
    transition: 200ms ease-in;
    margin-bottom: 15px;
    cursor: pointer;
}
.active{
    background-color: #6C5DD3;
    color: white;
}
.adminTools ul li:hover{
    background-color: #6C5DD3;
    color: white;
}
