.Add_client {
    display: none;
    transition: 400ms ease-in-out;
    background-color: rgba(0, 0, 0, 0.479);
    position: fixed;
    width: 100vw;
    height: 100vh;
    top: 0%;
    left: 0%;
    right: 0%;
    z-index: 10001;
}
.client_form {
    width: 50%;
    height: 50%;
    transition: 400ms ease-in-out;
    background-color: white;
    position: fixed;
    top: 30%;
    left: 35%;
    right: 25%;
    background-color: rgb(36, 39, 49);
    display: flex;
    align-items: center;
    flex-direction: column;
    border-radius: 10px;
    color: white;
    font-size: xx-large;
}
.action_bar{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin-left: 1vw;
    width: 75%;
    margin-right: 1vw;
}
.action_bar button{
    height: 30px;
    width: 30px;
    border: none;
    color: white;
    border-radius: 15px;
    background-color: rgb(77, 82, 100);
    transition: 400ms ease-in-out;
}
.action_bar button:hover{
    background-color: rgb(109, 0, 0);
}
.client_form form{
    display: flex;
    flex-direction: column;
    width: 75%;
}
.client_form input{
    margin: 5px;
    height: 25px;
    border-radius: 5px;
    border: none;
    padding: 5px;
    background-color: rgb(34, 35, 39);
    color: rgb(255, 255, 255);
    font-size: large;
}
.client_form select{
    height: 45px;
    margin: 5px;
    border-radius: 5px;
    border: none;
    padding: 5px;
    background-color: rgb(34, 35, 39);
    color: rgb(255, 255, 255);
    font-size: large;
}
.client_form form button{
    height: 45px;
    margin: 15px;
    border-radius: 5px;
    border: none;
    padding: 5px;
    background-color: rgb(44, 56, 102);
    color: rgb(255, 255, 255);
    font-size: large;
    cursor: pointer;
}