.Client_table {
    width: 75vw;
    border-radius: 10px;
    margin: 20px;
    box-shadow: 0px 40px 40px #181a2093;
    margin-left: 5vw;
    margin-right: 5vw;
}
.Client_table table{
    width: 75vw;
    color: #B2B3BD;
    padding: 2vw;
}
.Client_table th{
    font-size: 13px;
    font-weight: 500;
    text-align: left;
    margin-bottom: 50vh;
    
}
.printInvoice{
    cursor: pointer;
    box-shadow: 0px 10px 20px #0d0e11da;
    max-width: 30px;
    max-height: 30px;
    padding: 5px;
    border-radius: 25px;
    display: flex;
    justify-content: center;
    align-items: center;
}
td img{
    height: 80px;
    width: 80px;
    background-color: white;
    border-radius: 20px;
    margin-top: 10px;
    margin-left: 10px;
    margin-right: 10px;
}
.tdrow{
    display: flex;
    flex-direction: row;
    align-items: center;
}
.tdCol{
    display: flex;
    flex-direction: column;
}
td{
    text-align: left;
    box-shadow: inset 0px -2px 0px #e4e4e42c;
}