.FinancePage {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
}
.navbar{
    width: 75vw;
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: rgba(189, 255, 9, 0.452);
    font-size: x-large;
}
.navbar img{
    width: 60px;
    height: 60px;
    border-radius: 30px;
}
.revenue{
    width: 75vw;
    display: flex;
    justify-content: space-between;
    color: white;
    margin-top: 10vh;
    font-size: xx-large;
}
.transactions{
    margin-top: 10vh;
    width: 75vw;
    color: rgba(255, 255, 255, 0.6);
}
.transactions table{
    width: 75vw;
    box-shadow: 0px 40px 40px #181a2093;
    border-radius: 10px;
    padding: 25px;
}
.transactions table thead th{
    font-size: medium;
    font-weight: bold;
}
.transactions table td{
    padding: 20px;
    box-shadow: none;
}