.Internal_nav {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
}
.cp_switch button{
    color: rgb(139, 139, 139);
    border: none;
    width: 84px;
    height: 36px;
    background-color: rgb(36, 39, 49);
    border-radius: 12px;
    transition: 500ms;
}
.cp_switch .active_section{
    background-color: #6C5DD3;
    color: white;
}
.user_add{
    display: flex;
}
.user_add button{
    width: 40px;
    height: 40px;
    background: #3F8CFF;
    color: white;
    border: none;
    border-radius: 20px;
    margin: 5px;
    cursor: pointer;
    font-size: x-large;
    transition: 400ms ease-in;
}
.user_add button:hover{
    background: #64a2ff;
    box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.308);
}
.user_add img{
    width: 40px;
    height: 40px;
    background: #3F8CFF;
    color: white;
    border: none;
    border-radius: 20px;
    margin: 5px;
}