.Tool_bar {
    display: flex;
    max-width: 85vw;
    justify-content: space-evenly;
    margin-top: 20vh;
    margin-left: 5vw;
    margin-right: 5vw;

}

.Tool_bar select{
    width: 20vw;
    height: 56px;
    border-radius: 16px;
    padding: 10px;
    background-color: #e4e4e417;
    border: none;
    color: white;
}
.Tool_bar option{
    padding: 10px;
    width: 235px;
    height: 56px;
    background-color: #e4e4e417;
    color: white;
}
.Tool_bar input{
    width: 20vw;
    height: 36px;
    border-radius: 16px;
    padding: 10px;
    background-color: #e4e4e417;
    color: white;
    border: none;
}